import * as THREE from 'three';
import { GLTFLoader } from 'three/addons/loaders/GLTFLoader.js';
import {HelpControl} from "./HelpControl";


export class ThreeJSUtils {

    constructor() {
        this.cubes = []
        this.scene = this.createScene();
        this.camera =this.createCamera();
        this.renderer =  this.createRenderer();
        this.controls = null;
        this.animate = () =>{};
        this.createLight();
        this.createWalls();
        this.loadModel();
        window.addEventListener('resize', this.onWindowResize.bind(this));
    }

    anim(){
        this.checkCollisions();
        this.controls.update();
        this.checkCollisions();
        this.renderer.render( this.scene, this.camera );
    }

    createCamera(){
        let camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.01, 1000);
        camera.position.set(7, 10, 3);
        this.scene.add(camera);
        return camera
    }

    createScene(){
        let scene =  new THREE.Scene();
        scene.background = new THREE.Color(0xffffff);
        scene.fog = new THREE.Fog(0xffffff, 0, 750);
        return scene;
    }

    createRenderer(){
        let renderer = new THREE.WebGLRenderer({ antialias: true });
        renderer.setPixelRatio(window.devicePixelRatio);
        renderer.setSize(window.innerWidth, window.innerHeight);
        renderer.render(this.scene, this.camera);
        document.body.appendChild(renderer.domElement);
        renderer.render(this.scene, this.camera);
        return renderer;
    }

    loadModel() {
        const modelPath = 'https://etoart.github.io/museumdefence/models/comp.glb';
        const progressBar = document.getElementById('loading-bar-progress');
      
        caches.open('my-cache').then(cache => {
          cache.match(modelPath).then(response => {
            if (!response) {
              const loader = new THREE.GLTFLoader();
              loader.load(modelPath,
                (gltf) => {
                  const model = gltf.scene;
                  model.scale.set(6, 6, 6);
                  this.scene.add(model);
      
                  cache.put(modelPath, new Response(JSON.stringify(gltf.clone())));
                },
                (xhr) => {
                  const progress = (xhr.loaded / xhr.total) * 100;
                  progressBar.style.width = progress + '%';
                },
                (error) => {
                  console.log('  : ' + error.message);
                }
              );
            } else {
              response.json().then(data => {
                const cachedGltf = new THREE.GLTFLoader().parse(data);
                const model = cachedGltf.scene;
                model.scale.set(6, 6, 6);
                this.scene.add(model);
              });
            }
          });
        }).catch(error => {
          console.log(' : ' + error.message);
        });
      
        this.controls = new HelpControl(this.camera);
        this.animate = this.anim;
      }
      

    createLight(){
        const light = new THREE.HemisphereLight(0xeeeeff, 0x777788, 1.2);
        light.position.set(0.5, 1, 0.75);
        this.scene.add(light);
    }

    createWalls() {
        const material = new THREE.MeshBasicMaterial({visible:false});
        const wallData = [
            { position: [0, 0, 15], size: [75, 30, 6], rotation: [0, 0, 0] },
            { position: [-28.5, 0, -15], size: [20, 30, 6], rotation: [0, 0, 0] },
            { position: [21, 0, -15], size: [40, 30, 4], rotation: [0, 0, 0] },
            { position: [0, 0, -32], size: [75, 30, 10], rotation: [0, 0, 0] },
            { position: [38, 0, 0], size: [70, 30, 10], rotation: [0, Math.PI / 2, 0] },
            { position: [-37, 0, 0], size: [70, 30, 10], rotation: [0, Math.PI / 2, 0] },
            { position: [0, 0, 0], size: [7, 30, 7], rotation: [0, Math.PI / 2, 0] }
        ];

        wallData.forEach((data) => {
            const cube = new THREE.Mesh(new THREE.BoxGeometry(...data.size), material);
            cube.position.set(...data.position);
            cube.rotation.set(...data.rotation);
            this.scene.add(cube);
            this.cubes.push(cube);
        });
    }


    onWindowResize() {
        this.camera.aspect = window.innerWidth / window.innerHeight;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize(window.innerWidth, window.innerHeight);
    }

    checkCollisions() {
        const originPoint = this.camera.position.clone();
        const directions = [
            { direction: new THREE.Vector3(1, 0, 0), axis: 'x', offset: -0.1 },
            { direction: new THREE.Vector3(-1, 0, 0), axis: 'x', offset: 0.1 },
            { direction: new THREE.Vector3(0, 0, -1), axis: 'z', offset: 0.1 },
            { direction: new THREE.Vector3(0, 0, 1), axis: 'z', offset: -0.1 }
        ];

        directions.forEach(({ direction, axis, offset }) => {
            const raycaster = new THREE.Raycaster(originPoint, direction);
            const intersects = raycaster.intersectObjects(this.cubes);

            if (intersects.length > 0 && intersects[0].distance < 1) {
                this.camera.position[axis] += offset;
            }
        });
    }
}
