
import * as THREE from 'three';
import {DeviceOrientationControls} from "../jsm/DeviceOrientationControls";
import {PointerLockControls} from "three/addons/controls/PointerLockControls.js";
import { VirtualJoystick } from './virtualjoystik.js';

export class HelpControl {

    constructor(camera) {
        this.blocker  = document.getElementById( 'blocker' );
        this.instructions = document.getElementById( 'instructions' );
        this.camera = camera;
        this.joystick = null;
        this.controls = null;
        this.moveForward = false;
        this.moveBackward = false;
        this.moveLeft = false;
        this.moveRight = false;
        this.update = ()=>{};
        this.prevTime = performance.now();
        this.velocity = new THREE.Vector3();
        this.direction = new THREE.Vector3();

        if(this.isMobile() ){
            this.createMobileControls();
        }else{
            this.createDesktopControls();
        }
    }

    createDesktopControls(){

        this.changeSpanText();
        this.controls = new PointerLockControls( this.camera, document.body );

        this.instructions.addEventListener( 'click', () => {
            this.controls.lock();
            this.openFullscreen();
        } );

        this.controls.addEventListener( 'unlock', () => {

            this.blocker.style.display = 'block';
            this.instructions.style.display = '';

        } );

        this.controls.addEventListener( 'lock', () => {
            this.clearBlocker();
        } );
        this.update = this.updateDesktop;
        document.addEventListener('keydown', this.onKeyDown.bind(this), false);
        document.addEventListener('keyup', this.onKeyUp.bind(this), false);
    }

    createMobileControls(){
        this.changeSpanText();
        this.instructions.addEventListener( 'click',  () => {
            this.openFullscreen();
            this.clearBlocker();
            this.controls = new DeviceOrientationControls( this.camera );
            this.joystick =  new VirtualJoystick({
                container: document.body,
                mouseSupport: true,
            });
            this.update = this.updateMobile;
        }, false );
    }

    clearBlocker(){
        this.instructions.style.display = 'none';
        this.blocker.style.display = 'none';
    }

    changeSpanText(){
        const spanElement = document.getElementById('text-span');
        this.instructions.style.backgroundColor = "rgba(255,255,255,1)";

        spanElement.innerText = "  ";
    }

    openFullscreen() {
        const element = document.documentElement;

        if (element.requestFullscreen) {
            element.requestFullscreen().then(() => {});
        } else if (element.webkitRequestFullscreen) {
            element.webkitRequestFullscreen();
        } else if (element.mozRequestFullScreen) {
            element.mozRequestFullScreen();
        } else if (element.msRequestFullscreen) {
            element.msRequestFullscreen();
        }
    }

    isMobile() {
       return (/Android|webOS|iPhone|iPad|iPod|BlackBerry|BB|PlayBook|IEMobile|Windows Phone|Kindle|Silk|Opera Mini/i.test(navigator.userAgent));
    }

    updateMobile() {
        this.controls.update();
        let deltaX = this.joystick.deltaX(); //  deltaX  

        if ((deltaX > -40 && deltaX < 0) || (deltaX > 0 && deltaX < 40)) {
            deltaX = 0;
        }
        let deltaY = this.joystick.deltaY();  //  deltaY  
        const movementSpeedY = 0.000001;
        const movementSpeedX = 0.000001;
        const time = performance.now();
        //    
        const cameraDirection = new THREE.Vector3();
        this.camera.getWorldDirection(cameraDirection);

        //  ,    
        const cameraRight = new THREE.Vector3();
        cameraRight.crossVectors(cameraDirection, new THREE.Vector3(0, 1, 0));

        //       deltaX,     
        const rightMovement = cameraRight.multiplyScalar(deltaX * time * movementSpeedX);

        //    /        deltaY,     
        const forwardMovement = cameraDirection.multiplyScalar(-deltaY * time * movementSpeedY);
        const maxMovement = 0.1; //    

        //    `forwardMovement`  `maxMovement`
        if (forwardMovement.length() > maxMovement) {
            forwardMovement.normalize().multiplyScalar(maxMovement);
        }

        //    `rightMovement`  `maxMovement`
        if (rightMovement.length() > maxMovement) {
            rightMovement.normalize().multiplyScalar(maxMovement);
        }

        //   ,        
        this.camera.position.add(forwardMovement);
        this.camera.position.add(rightMovement);
        this.camera.position.y = 10;
    }


    updateDesktop() {
        const time = performance.now();

        if (this.controls.isLocked === true) {
            const delta = (time - this.prevTime) / 500;

            this.velocity.x -= this.velocity.x * 75.0 * delta;
            this.velocity.z -= this.velocity.z * 75.0 * delta;

            this.velocity.y -= 9.8 * 100.0 * delta; // 100.0 = mass

            this.direction.z = Number(this.moveForward) - Number(this.moveBackward);
            this.direction.x = Number(this.moveRight) - Number(this.moveLeft);
            this.direction.normalize(); // this ensures consistent movements in all directions

            if (this.moveForward || this.moveBackward) this.velocity.z -= this.direction.z * 400.0 * delta;
            if (this.moveLeft || this.moveRight) this.velocity.x -= this.direction.x * 400.0 * delta;

            this.controls.moveRight(-this.velocity.x * delta);
            this.controls.moveForward(-this.velocity.z * delta);
            this.controls.getObject().position.y = 10;
        }

        this.prevTime = time;
    }



    onKeyDown(event) {

        switch ( event.code ) {

            case 'ArrowUp':
            case 'KeyW':
                this.moveForward = true;
                break;

            case 'ArrowLeft':
            case 'KeyA':
                this.moveLeft = true;
                break;

            case 'ArrowDown':
            case 'KeyS':
                this.moveBackward = true;
                break;

            case 'ArrowRight':
            case 'KeyD':
                this.moveRight = true;
                break;
    }
    }

    onKeyUp(event) {

        switch ( event.code ) {

            case 'ArrowUp':
            case 'KeyW':
                this.moveForward = false;
                break;

            case 'ArrowLeft':
            case 'KeyA':
                this.moveLeft = false;
                break;

            case 'ArrowDown':
            case 'KeyS':
                this.moveBackward = false;
                break;

            case 'ArrowRight':
            case 'KeyD':
                this.moveRight = false;
                break;

        }
    }
}